<template>
  <div id="bodyBox">
    <div class="entry_card">
      <span>姓名</span>
      <input class="el-input__inner"
             type="text"
             v-model="data.username" />
    </div>
    <div class="entry_card">
      <span>年龄</span>
      <input class="el-input__inner"
             type="text"
             v-model="data.age" />
    </div>
    <div class="entry_card">
      <span>性别</span>
      <div>
        <el-radio v-model="data.sex"
                  label="男">男</el-radio>
        <el-radio v-model="data.sex"
                  label="女">女</el-radio>
      </div>
    </div>
    <div class="entry_card">
      <span>部门</span>
      <div>
        <el-select v-model="data.department"
                   placeholder="请选择部门">
          <el-option label="前端开发"
                     value="前端开发"></el-option>
          <el-option label="测试工程师"
                     value="测试工程师"></el-option>
        </el-select>
      </div>
    </div>
    <div class="entry_card">
      <span>居住地</span>
      <input class="el-input__inner"
             type="text"
             v-model="data.address" />
    </div>
    <div class="entry_card">
      <span>是否健康</span>
      <div>
        <el-radio v-model="data.health"
                  label="是">是</el-radio>
        <el-radio v-model="data.health"
                  label="否">否</el-radio>
      </div>
    </div>
    <div class="entry_card">
      <el-button type="primary"
                 @click="submitForm()">点击添加</el-button>
      <el-button @click="resetForm()">重置</el-button>
    </div>
    <router-link to="/p_home">
      <el-button type="primary"
                 icon="el-icon-arrow-left">返回员工列表</el-button>
    </router-link>
  </div>
</template>
<script>
import { postEntry } from "@/api/api";
export default {
  data() {
    return {
      data: {
        username: "",
        age: "",
        sex: "",
        department: "",
        address: "",
        health: ""
      }
    };
  },
  methods: {
    submitForm() {
      console.log(this.data);
      if (
        this.data.username === "" ||
        this.data.age === "" ||
        this.data.sex === "" ||
        this.data.department === "" ||
        this.data.address === "" ||
        this.data.health === ""
      ) {
        this.$message({
          showClose: true,
          message: "请检查你的输入框,不能有空哦",
          type: "warning"
        });
      } else if (!new RegExp("^[0-9]*$").test(this.data.age)) {
        this.$message({
          showClose: true,
          message: "年龄只能是数字",
          type: "warning"
        });
      } else {
        postEntry(this.data).then(data => {
          this.$message({
            showClose: true,
            message: data.data,
            type: "success"
          });
        });
        for (const key in this.data) {
          this.data[key] = "";
        }
      }
    },
    resetForm() {
      for (const key in this.data) {
        this.data[key] = "";
      }
    }
  }
};
</script>
<style scoped>
#bodyBox {
  padding: 80px;
  width: 500px;
  text-align: left;
}
.entry_card {
  height: 70px;
}
</style>